<template>
  <div id="app">
    <div class="ui container">
      <div class="ui grid">
        <leftSide></leftSide>
        <router-view></router-view>
      </div>
    </div>
  </div>
</template>

<script>
import leftSide from '@/components/layout/leftside';

export default {
  name: 'Index',
  components: {
    leftSide,
  },
  mounted() {
    this.$nprogress.done();
    this.$common.fadeInEffect(document.querySelector('#app'));
  },
};
</script>

<style scoped>
#app {
  flex: 1;
  margin-top: 60px;
  opacity: 0;
  -webkit-transition: opacity 1s, z-index 0 1s;
}
</style>



